<template>
  <div id="container">
    <div id="settings">
      <div class="user-pic">
        <img v-if="user.photo !== 'https://server.gold-ticket.uz/storage/app/'" :src="user.photo" alt="User Pic" @click.prevent="this.update()">
        <img v-else src="https://gold-ticket.uz/tgs/avatar.png" alt="User Pic" @click.prevent="this.update()">
      </div>
<!--      <div class="user-name">{{ this.first_name }} {{ this.last_name }}</div>-->
      <form class="settings-form" action="">
        <div class="form-control">
          <label class="form-label" for="name" v-if="user.lang === 'ru'">Имя</label>
          <label class="form-label" for="name" v-else>Isim</label>
          <input class="form-input" id="name" name="name" v-model="user.name" type="text">
        </div>
        <div class="form-control">
          <label class="form-label" for="phone" v-if="user.lang === 'ru'">Телефон</label>
          <label class="form-label" for="phone" v-else>Telefon</label>
          <input class="form-input" id="phone" name="phone" v-model="user.phone" disabled type="text">
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "Settings",
  data: () => ({
    user_id: 0,
    user:null,
    name: '',
    phone: '',
  }),
  async mounted() {
    const telegram = window.Telegram.WebApp;
    const telegramData = telegram.initDataUnsafe;
    const mainButton = telegram.MainButton;
    const backButton = telegram.BackButton;

    // this.user_id = 5296876609
    this.user_id = telegramData.user.id

    await axios.get('https://server.gold-ticket.uz/api/user/' + this.user_id)
        .then(response => {
          this.user = response.data.data
        })
        .catch(error => console.log(error));

    backButton.show()
    backButton.onClick(() => {
      this.$router.push('/')
    });

    mainButton.show()
    mainButton.setParams({
      'text': "Сохранить",
      'color': "#f2c058",
      'text_color': "#FFF"
    })
    mainButton.onClick(() => {
      mainButton.showProgress()
      axios
          .post('https://server.golf-tiket.uz/api/user/' + this.user_id, {
            first_name: this.first_name,
            last_name: this.last_name,
            email: this.email
          })
          .then(response => {
            this.$router.push('/')
          })
          .catch(error => {
            telegram.showPopup(error)
          })
    })
  }
}
</script>

<style scoped>
#container {
  display: flex;
  justify-content: center;
  align-items: center;
}

@media screen and (min-height: 734px) {
  #container {
    width: 100%;
    height: auto;
    padding: 0 35px;
    margin-bottom: 0;
    position: relative;
    top: 90px;
  }
}

@media screen and (min-height: 700px) {
  #container {
    width: 100%;
    height: auto;
    padding: 0 35px;
    margin-bottom: 0;
    position: relative;
    top: 90px;
  }
}

@media screen and (min-height: 498px) {
  #container {
    width: 100%;
    height: auto;
    padding: 0 35px;
    margin-bottom: 200px;
    position: relative;
    top: 80px;
  }
}
</style>
